@import '../assets/variables';

.about-content {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.about-content__div__1 {
    background-color: $color-primary;
    height: 100vh;
    width: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 50px;
    border-left: 5px solid $color-accent;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    color: $color-secondary;
}

.about-content__div__1::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.8));
    transform: translateX(-100%);
    transition: transform 0.5s ease-in-out;
}

.about-content__div__1:hover::before {
    transform: translateX(0);
}

.about-content__div__1 p {
    margin: 0;
    padding: 0;
    font-size: $font-size-large;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
}

.about-text__1p {
    font-size: $font-size-medium;
    line-height: 1.6;
    padding-top: 20px;
    animation: fadeIn 1.5s ease-in-out;
}

.about-content__div__2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-left: 5px solid $color-accent;
    position: relative;
    overflow: hidden;
    background-color: $color-primary;
    margin-top: 30px;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform $transition-speed ease-in-out;
    width: fit-content;
}

.about-content__div__2:hover {
    transform: translateY(-5px);
}

.about-content__div__2__p {
    font-size: $font-size-large;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    margin: 0;
    margin: 10px;
    padding: 0;
    border-radius: 10px;
    text-align: center;
    padding: 50px;
    animation: fadeIn 1.5s ease-in-out;
    color: $color-secondary;
}

.about__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.projects {
    border-radius: 10px;
    overflow: hidden;
    width: 45%;
    margin: 20px;
    transition: transform $transition-speed ease;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.projects:hover {
    box-shadow: rgba(0, 0, 0, 0.17) inset 0px 0px 25px 0px, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    transform: scale(1.1);
}

.projects:hover .project-title-hover {
    color: $color-accent;
}

@media (max-width: 768px) {
    .projects {
        width: 100%; 
    }
}

@media (max-width: 480px) {
    .about-content__div__2__p {
        font-size: $font-size-medium;
    }
}

@media (max-width: 820px) {
    .projects {
        width: 100%;
    }

    .about__grid {
        display: flex;
        flex-direction: column;
        width: 100%; 
    }

    .about-content__div__2 {
        width: 100%;
        margin-top: 20px;
        padding: 20px;
    }
}
